import React, { Component } from 'react';
import { Esrooms } from '../Rooms/Esrooms';

class Header extends Component{
	shouldComponetUpdate(nextProps, nextState) {
		return false;
	}
	render () {
		return (
			<div>
				<h1 >Escape Team!</h1>
				<div>Rooms played: {Esrooms.length}</div>
			</div>	
		)
	}
}

export default Header


